import request from '@/utils/request';
var api = {
  GetList: '/api/dms/UserFeedback/GetListByCompanyId',
  ExportPicture: '/api/dms/UserFeedback/ExportPicture',
  FeedbackResponseCreate: '/api/dms/FeedbackResponse/Create',
  GetFeedbackByFeedbackId: '/api/dms/FeedbackResponse/GetFeedbackByFeedbackId',
  GetListByEmployeeId: '/api/dms/UserFeedback/GetListByEmployeeId'
}; // 获取机构下反馈列表

export function GetList(obj) {
  return request({
    url: api.GetList,
    method: 'get',
    params: obj
  });
} // 根据反馈ID获取反馈信息

export function GetFeedbackByFeedbackId(feedbackId) {
  return request({
    url: api.GetFeedbackByFeedbackId,
    method: 'get',
    params: {
      feedbackId: feedbackId
    }
  });
} // 图片导出

export function ExportPicture(cInformation) {
  return request({
    url: api.ExportPicture,
    method: 'get',
    params: {
      cInformation: cInformation
    }
  });
} // 后台回复提交

export function FeedbackResponseCreate(obj) {
  return request({
    url: api.FeedbackResponseCreate,
    method: 'post',
    data: obj
  });
} // 根据反馈ID获取反馈信息

export function GetListByEmployeeId() {
  return request({
    url: api.GetListByEmployeeId,
    method: 'get'
  });
}